<template>
    <div class="scLanmu">
      <ul class="tab_list">
        <li class="lantype" @click="zonghe()" :class="searchInfo.sort==='' ? 'active' : ''"><i>综合</i> </li>
        <li @click="changedt()">
          <i :class="(searchInfo.sort===1||searchInfo.sort===2) ? 'active' : ''">销量</i>
          <div>
            <span class="iconfont icon-shangsanjiao" :class="searchInfo.sort===1 ? 'active' : ''"></span>
            <span class="iconfont icon-xiasanjiao" :class="searchInfo.sort===2 ? 'active' : ''"></span>
          </div>
        </li>
        <li @click="changemn()" >
          <i :class="(searchInfo.sort===3||searchInfo.sort===4)? 'active' : ''">积分</i>
          <div>
            <span class="iconfont icon-shangsanjiao" :class="searchInfo.sort===3 ? 'active' : ''"></span>
            <span class="iconfont icon-xiasanjiao" :class="searchInfo.sort===4 ? 'active' : ''"></span>
          </div>
        </li>
        <li @click="popupVisible=true" :class="(searchInfo.sort===5)? 'active' : ''"><i>筛选</i> </li>
      </ul>
      <div class="lanList">
        <MyLoadMore :list='goodList'  @is-loadmore = 'loadmore' :isLoad='isLoad'>
        <ul v-if="goodList.length>=1">

          <router-link tag="li"  :to="{name:'spXiangqin',params:{id:item.id}}"  v-for="(item,index) in goodList" :key="index">
            <img :src="(item.goods_img&&item.goods_img.length>0)?item.goods_img[0]:''" alt="">
            <h3 class="danyi">
              {{item.goods_name}}
            </h3>
            <div class="sc_jflist">
              <div class="jfnum"><span>{{item.goods_integral}}</span>积分</div>
              <div class="rsnum"><span>{{item.goods_inventory}}</span>人已兑换</div>
            </div>
          </router-link>

        </ul>
        </MyLoadMore>
      </div>
      <a href="" class="kefu iconfont icon-kefu"></a>
      <mt-popup
        v-model="popupVisible"
        position="right"
      >
        <div class="po_warp">
          <form @submit.prevent="submitForm">
            <div class="bottom_type">
              <van-button type="default" @click.prevent="resetSearch">重置</van-button>
              <van-button type="default" native-type="submit" >确认</van-button>
            </div>
            <div class="info">积分区间</div>
            <div class="money_area">
              <input type="number" v-model="searchInfo.min" min="0" placeholder="最低积分">
              <span></span>
              <input type="number" v-model="searchInfo.max" :min="searchInfo.min" placeholder="最高积分" >
            </div>
            <ul class="jifenlist">
              <li @click="chioseThis(index)" :class="( (index===moneyList.length-1)? (searchInfo.min==2500&&searchInfo.max=='') : (item.split('-')[0]==searchInfo.min && item.split('-')[1]==searchInfo.max )) ?'chiose':'' "  v-for="(item,index) in moneyList" :key="index">
                {{item}}
              </li>
            </ul>
          </form>
        </div>
      </mt-popup>
    </div>
</template>

<script>
import { apiGet } from '../common/js/api';
import  MyLoadMore  from '@/common/MyLoadMore'
import { Toast } from "vant";
    export default {
        name: "sc_lanmu",
      components:{ MyLoadMore },
      data(){
          return{
            query:this.$route.query,
            popupVisible: false,
            sort: 'zonghe',
            moneyId: '',
            isLoad:true,
            mnListnum:-1,
            highcode:'',
            lowcode:'',
            searchInfo:{
              page:1,
              size:10,
              cate_id:this.$route.query.cate_id,
              key:this.$route.query.key,
              sort:"",
              min:"",
              max:"",
            },
            moneyList:['0-100','100-300','300-500','500-1000','1000-2000','2500以上'],
            goodList: [

            ]
          }
      },
      created(){
        this.getGoodsList();
      },
      methods: {
        //获取商品列表
        getGoodsList(){
          apiGet("/goods/goods-list",{
            ...this.searchInfo
          }).then(res=>{ 
            this.goodList = res.data.data.data;
          })
        },
        //根据范围搜索
        submitForm(){
          this.popupVisible=false;
          this.$set(this.searchInfo,'sort', 5)
          this.getGoodsList()
        },
        //加载更多
        loadmore(){
          this.$set(this.searchInfo,'page',++this.searchInfo.page)
          apiGet("/goods/goods-list",this.searchInfo ).then(res => {
            if (res.status === 200) {
              this.goodList = this.goodList.concat(res.data.data.data);
              if(res.data.data.data < 10) {
                this.isLoad = false;
              } else {
                this.isLoad = true
              }
            }
          })

        },
        //重置搜索条件
        resetSearch(){
          this.searchInfo = {
            page:1,
            size:10,
            key:this.query.key,
            cate_id:this.query.cate_id,
            sort:"",
            min:"",
            max:"",
          }
          this.popupVisible = false;
          this.getGoodsList();
        },
          // 综合
        zonghe () {
          this.$set(this.searchInfo,'sort', '')
          this.getGoodsList()
        },
        changedt () {
          this.searchInfo.page=1;
          const { sort } = this.searchInfo;
          this.$set(this.searchInfo,'sort', sort===1? 2 : (sort===2? 1 : 2))
          this.getGoodsList()
        },
        changemn () {
          this.searchInfo.page=1;
          const { sort } = this.searchInfo
          this.$set(this.searchInfo,'sort', sort===3? 4 : (sort===4? 3 : 4))
          this.getGoodsList()
        },
        chioseThis(index){
          this.mnListnum=index;
          if(this.mnListnum === this.moneyList.length-1 ){
            this.searchInfo.min ="2500"
            this.searchInfo.max =""
          }else{
            let num = this.moneyList[this.mnListnum].split("-")
            this.searchInfo.min =num[0]
            this.searchInfo.max =num[1]
          }
        }
      },
      mounted(){

      }
    }
</script>

<style scoped lang="scss">
  #app .content{
    background: #9a9a9a;
  }
  .active{
    color: #3C7FEE !important;
  }
  .mint-popup-right {
    width: 70%;
    height: 100%;
    background: white;
    .po_warp {
      position: relative;
      height: 100%;
      overflow: auto;
      .jifenlist{
          display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        li{
          width: 1.04rem;
          margin: 0 0.13rem 0.17rem;
          height: 0.5rem;
          background: #E5E8EB;
          text-align: center;
          display: block;
          font-size: 0.17rem;
          line-height: 0.5rem;
          border-radius: 0.04rem;
        }
        .chiose{
          border: 1px solid #3C7FEE;
          color: #3C7FEE;
        }
      }
      .info {
        height: 0.31rem;
        background: #efefef;
        line-height: 0.31rem;
        text-indent: 0.13rem;
        font-size: 0.15rem;
        color: #9a9a9a;
      }
    }
   .money_area{
      padding-top: 0.21rem;
      overflow: hidden;
      padding-bottom: 0.2rem;
      span{float: left;margin: 0.15rem 0.06rem;width: 0.16rem;height: 1px;background: #bcbcbc}
      input{
        width: 40%;
        font-size: 0.13rem;
        color: #9a9a9a;
        line-height: 0.31rem;
        border: 1px solid #bcbcbc;
        border-radius: 0.04rem;
        height: 0.31rem;
        text-indent: 0.09rem;
        float: left;
        &:nth-of-type(1){margin-left: 5%}
      }

    }
    .bottom_type{
      position: fixed;
      bottom: 0;
      display: flex;
      width: 100%;
      font-size: 0.19rem;
      height: 0.47rem;
      button{
        flex: 1;
        text-align: center;
        line-height: 0.47rem;
      }
      button:nth-of-type(1){
        color: #3C7FEE;
        border: none;
        border-top: 1px solid #f5f5f5;
      }
      button:nth-of-type(2){
        color: white;
        background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
      }
    }
  }
  .scLanmu{
    height: 100%;
    .tab_list{
      display: flex;
      position: fixed;
      top: 0;
      justify-content: space-between;
      height:0.47rem;
      width: 100%;
      background: white;
    li{flex: 1;line-height: 0.47rem;text-align: center;font-size: 0.19rem;
      display: flex;
      justify-content: center;
    i{font-style: normal}
    div{
      width: 0.3rem;
      display: flex;
      flex-direction: column;
      line-height: 0.34rem;
    span{
      font-size: 0.2rem;
      display: block;
      width: 0.3rem;
      color: #bcbcbc;
      height: 0.1rem;
    }
    .icon-shangsanjiao{
      font-size: 0.16rem;
    }
    .blue{
      color: #3C7FEE;
    }
    }
    }
      .textblue{
      color: #3C7FEE;
    }
    }
    .lanList{
      width: 100%;
      background: #E6E9ED;
      margin-top: 0.5rem;
      height: 100%;
      display: flex;
      overflow: auto;
      flex-wrap: wrap;
      ul{
      border-radius: 0.04rem;
        width: 3.5rem;
        margin: 0.21rem auto ;
        background: white;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding: 0.14rem 0.13rem;
      }
      li{
          width: 1.56rem;
        height: 2.32rem;
        border-bottom:1px solid #f5f5f5;
        img{
          display: block;
          width: 1.56rem;
          height: 1.59rem;
        }
        h3{
          width: 1.56rem;
          font-size: 0.16rem;
          font-weight: 600;
          color: #333;
          line-height: 0.34rem;
        }
        .sc_jflist{
          height: 0.4rem;
          display: flex;
          justify-content: space-between;
          .jfnum{text-align: left;font-size: 0.16rem;color: red}
          .rsnum{text-align: right;font-size: 0.12rem;line-height: 0.24rem;color: #9a9a9a}
        }
      }

    }
    .kefu{
      width: 0.52rem;
      height: 0.52rem;
      background:linear-gradient(-90deg,rgba(70,133,238,1),rgba(155,193,255,1));
      border-radius:50%;
      box-shadow:0px 5px 10px 0px rgba(60,127,238,0.42);
      position: fixed;
      bottom: 1.5rem;
      display: block;
      text-align: center;
      line-height: 0.52rem;
      font-size: 0.28rem;
      color: white;
      z-index: 3;
      right: 0.11rem;
    }
  }
</style>



